@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@100;300;400&family=Source+Code+Pro:wght@300;400&display=swap);
/* =========================================
              Website Resets
============================================ */
*,
*::after,
*::before {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
	-webkit-user-select: none;
	-ms-user-select: none;
	-o-user-select: none;
	user-select: none;
}

*::-moz-focus-inner {
	border: 0;
}

*:focus {
	outline: none;
}

/*=============================================
          Variables
================================================*/

:root {
	/* dark shades of primary color*/
	--clr-primary-1: hsl(205, 86%, 17%);
	--clr-primary-2: hsl(205, 77%, 27%);
	--clr-primary-3: hsl(205, 72%, 37%);
	--clr-primary-4: hsl(205, 63%, 48%);
	/* primary/main color */
	--clr-primary-5: hsl(205, 78%, 60%);
	/* lighter shades of primary color */
	--clr-primary-6: hsl(205, 89%, 70%);
	--clr-primary-7: hsl(205, 90%, 76%);
	--clr-primary-8: hsl(205, 86%, 81%);
	--clr-primary-9: hsl(205, 90%, 88%);
	--clr-primary-10: hsl(205, 100%, 96%);
	/* darkest grey - used for headings */
	--clr-grey-1: hsl(209, 61%, 16%);
	--clr-grey-2: hsl(211, 39%, 23%);
	--clr-grey-3: hsl(209, 34%, 30%);
	--clr-grey-4: hsl(209, 28%, 39%);
	/* grey used for paragraphs */
	--clr-grey-5: hsl(210, 22%, 49%);
	--clr-grey-6: hsl(209, 23%, 60%);
	--clr-grey-7: hsl(211, 27%, 70%);
	--clr-grey-8: hsl(210, 31%, 80%);
	--clr-grey-9: hsl(212, 33%, 89%);
	--clr-grey-10: hsl(210, 36%, 96%);
	--clr-white: #fff;
	--clr-red-dark: hsl(360, 67%, 44%);
	--clr-red-light: hsl(360, 71%, 66%);
	--clr-green-dark: hsl(125, 67%, 44%);
	--clr-green-light: hsl(125, 71%, 66%);
	--clr-black: #222;
	--transition: all 0.3s linear;
	--spacing: 0.1rem;
	--radius: 0.25rem;
	--light-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
	--dark-shadow: 0 5px 15px rgba(0, 0, 0, 0.2);
	--max-width: 1170px;
	--fixed-width: 620px;
	--font-body: 'Source Code Pro', monospace;
	--font-heading: 'Montserrat', sans-serif;
}

/*=============================================
          Global Styles
================================================*/

body {
	font-family: 'Source Code Pro', monospace;
	font-family: var(--font-body);
	background: hsl(210, 36%, 96%);
	background: var(--clr-grey-10);
	color: hsl(209, 61%, 16%);
	color: var(--clr-grey-1);
	line-height: 1.5;
	font-size: 0.875rem;
}
ul {
	list-style-type: none;
}
a {
	text-decoration: none;
}
h1,
h2,
h3,
h4 {
	font-family: 'Montserrat', sans-serif;
	font-family: var(--font-heading);
	letter-spacing: 0.1rem;
	letter-spacing: var(--spacing);
	text-transform: capitalize;
	line-height: 1.25;
	margin-bottom: 0.75rem;
}
h1 {
	font-size: 3rem;
}
h2 {
	font-size: 2rem;
}
h3 {
	font-size: 1.25rem;
}
h4 {
	font-size: 0.875rem;
}
p {
	margin-bottom: 1.25rem;
	color: hsl(210, 22%, 49%);
	color: var(--clr-grey-5);
}
@media screen and (min-width: 800px) {
	h1 {
		font-size: 4rem;
	}
	h2 {
		font-size: 2.5rem;
	}
	h3 {
		font-size: 1.75rem;
	}
	h4 {
		font-size: 1rem;
	}
	body {
		font-size: 1rem;
	}
	h1,
	h2,
	h3,
	h4 {
		line-height: 1;
	}
}

/**************** global classes ****************/
.section {
	width: 90vw;
	margin: 0 auto;
	max-width: 1170px;
	max-width: var(--max-width);
}

@media screen and (min-width: 992px) {
	.section {
		width: 95vw;
	}
}

/*=============================================
          Color Generator
================================================*/
.container {
	text-align: center;
	display: flex;
	align-items: center;
	height: 100px;
	padding-left: 2rem;
}
.container h3 {
	margin-bottom: 0;
	margin-right: 2rem;
}

input {
	border-color: transparent;
	padding: 0.5rem 1rem;
	font-size: 1.2rem;
	border-top-left-radius: 0.25rem;
	border-top-left-radius: var(--radius);
	border-bottom-left-radius: 0.25rem;
	border-bottom-left-radius: var(--radius);
}
.btn {
	background: hsl(205, 78%, 60%);
	background: var(--clr-primary-5);
	background: hsl(209, 34%, 30%);
	background: var(--clr-grey-3);
	padding: 0.5rem 1rem;
	font-size: 1.2rem;
	letter-spacing: 1px;
	border-color: transparent;
	border-top-right-radius: 0.25rem;
	border-top-right-radius: var(--radius);
	border-bottom-right-radius: 0.25rem;
	border-bottom-right-radius: var(--radius);
	text-transform: capitalize;
	color: #fff;
	color: var(--clr-white);
	cursor: pointer;
	transition: background-color 0.3s ease-in-out;
}
.btn:hover {
	background: hsl(209, 61%, 16%);
	background: var(--clr-grey-1);
}
@media screen and (max-width: 576px) {
	.container h3 {
		font-size: 1rem;
	}
	input,
	.btn {
		font-size: 0.85rem;
	}
}
@media screen and (max-width: 520px) {
	.container {
		padding-left: 0.25em;
		display: grid;
		grid-template-columns: 1fr;
		grid-template-rows: 2fr;
		grid-gap: 0;
		gap: 0;
		margin-bottom: 1em;
	}
	.container h3 {
		font-size: 0.8rem;
		margin-bottom: -2em;
	}
	input,
	.btn {
		font-size: 0.6rem;
	}
}
input.error {
	border: 2px solid hsl(360, 67%, 44%);
	border: 2px solid var(--clr-red-dark);
}
.colors {
	min-height: calc(100vh - 100px);
	display: grid;
	grid-template-columns: repeat(auto-fit, minmax(223.33px, 1fr));
	grid-template-rows: repeat(auto-fit, minmax(96px, 1fr));
}
.color {
	padding: 1rem 2rem;
	cursor: pointer;
	font-size: 1rem;
	text-transform: none;
}
.percent-value {
	margin-bottom: 0;
	color: hsl(209, 61%, 16%);
	color: var(--clr-grey-1);
}
.color-value {
	color: hsl(209, 61%, 16%);
	color: var(--clr-grey-1);
	margin-bottom: 0;
}
.color-light .color-value {
	color: #fff;
	color: var(--clr-white);
}
.color-light .percent-value {
	color: #fff;
	color: var(--clr-white);
}

.alert {
	text-transform: uppercase;
	font-size: 0.85rem;
	margin-top: 0.5rem;
}

